import styled from "styled-components"
import colors from "./colors"
import fonts from "./fonts"

export const Container = styled.div`
    width: 100%;
    height: max-content;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    font-family: ${fonts.outlined};

    & > #header {
        width: 100%;
        height: 75px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: ${colors.black};
        position: fixed;
        border-bottom: 1px;
        border-top: 0;
        border-left: 0;
        border-right: 0;
        border-style: solid;
        border-color: transparent;

        & > div{
            width: 15%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            border-bottom: 2px;
            border-top: 0;
            border-left: 0;
            border-right: 0;
            border-style: solid;
            border-color: ${colors.gray};
            cursor: pointer;
            font-family: ${fonts.normal};
            color: whitesmoke;

            :hover{
                border-color: ${colors.red};
            }
        }
    }

    @media (max-width: 425px){
        & > #header{
            & > div{
                width: 23%;
            }
        }
    }

    @media (max-width: 375px){
        & > #header{
            & > div{
                font-size: 15px;
                width: 23%;
            }
        }
    }
    @media (max-width: 320px){
        & > #header{
            & > div{
                font-size: 15px;
                width: 23%;
            }
        }
    }
`

export const Body = styled.div`
    width: 100%;
    height: max-content;
    color: ${colors.contrast};
    overflow: auto;

    & > #about {
        width: 100%;
        height: 100vh;
        background-color: ${colors.black};
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        
        & > h1 {
            font-weight: bold;
            font-size: 81px;
            margin: 0;
        }

        & > h3 {
            font-weight: bold;
            font-size: 32px;
        }

        & > a {
            text-decoration: none;
            color: ${colors.contrast};
            font-weight: bold;
            font-family: ${fonts.normal};
            font-size: 22px;

            :hover {
                filter: opacity(75%);
            }
        }

        & > div {
            width: 150px;
            display: flex;
            justify-content: space-between;

            & > a{
                transition: 250ms;
                :hover {
                    filter: opacity(75%)
                }
            }
        }
    }

    @media (max-width: 768px){
        & > #about{
            & > h1 {
                font-size: 64px;
            }
        }
    }

    @media (width: 425px){
        & > #about{
            & > h1 {
                font-size: 32px;
            }

            & > h3{
                font-size: 24px;
            }
        }
    }

    @media (width: 375px){
        & > #about{
            & > h1 {
                font-size: 28px;
            }

            & > h3{
                font-size: 22px;
            }
        }
    }

    @media ( width: 320px){
        & > #about{
            & > h1 {
                font-size: 24px;
            }

            & > h3{
                font-size: 18px;
            }
        }
    }

//===================================================================================================================================================================//

    & > #education {
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: ${colors.contrast};
        color: ${colors.black};
        font-family: ${fonts.normal};

        & > h1 {
            font-family: ${fonts.outlined};
            font-size: 42px;
        }

        & > ul {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding: 0;
            margin: 0;
            font-size: 20px;
        }
    }

    @media (width: 425px){
        & > #education{
            & > ul{
                & > li{
                    font-size: 16px;
                    text-align: center;
                }
            }
        }
    }

    @media (width: 375px){
        & > #education{
            & > ul{
                & > li{
                    font-size: 16px;
                    text-align: center;
                }
            }
        }
    }
    
    @media (width: 320px){
        & > #education{
            & > ul{
                & > li{
                    font-size: 16px;
                    text-align: center;
                }
            }
        }
    }

//===================================================================================================================================================================//

    & > #skills {
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: ${colors.black};
        color: ${colors.contrast};
        font-family: ${fonts.normal};

        & > div {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 70%;
            height: 100%;

            & > h1 {
                margin-top: 100px;
                font-family: ${fonts.outlined};
                font-size: 48px;
            }

            & > div {
                width: 100%;
                height: 100%;

                display: grid;
                grid-template-columns: 1fr 1fr 1fr 1fr;
                justify-content: center;
                align-items: center;

                & > div {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;

                    & > h4 {
                        font-size: 18px;
                    }
                }
            }
        }
    }

    @media (width: 425px){
        & > #skills{
            & > div{
                width: 97%;
            }
        }
    }

    @media (width: 375px){
        & > #skills{
            & > div{
                width: 97%;
                & > div{
                    grid-template-columns: 1fr 1fr 1fr;
                    & div{
                        & > #react_native{
                            text-align: center;
                        }
                    }
                }
            }
        }
    }

    @media (width: 320px){
        & > #skills{
            & > div{
                width: 97%;
                & > div{
                    grid-template-columns: 1fr 1fr 1fr;
                    & div{
                        & > #react_native{
                            text-align: center;
                        }
                    }
                }
            }
        }
    }

//===================================================================================================================================================================//

    & > #publications {
        width: 100%;
        height: max-content;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: ${colors.contrast};
        color: ${colors.black};
        font-family: ${fonts.normal};

        & > h1 {
            font-family: ${fonts.outlined};
            font-size: 48px;
            margin-top: 100px;
        }

        & > div {
            width: 100%;
            height: 100vh;
            display: flex;
            justify-items: center;
            align-items: center;

            & > div{
                width: 50%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;

                & > h1 {
                    font-family: ${fonts.outlined};
                    font-size: 48px;
                }

                & > div {
                    width: 50%;
                    justify-content: center;
                    align-items: center;
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr;
                    margin-left: 40px;
                }

                & > a {
                    text-decoration: none;
                    font-size: 20px;
                    cursor: pointer;
                    margin-top: 50px;
                    color: ${colors.red};
                    
                    :hover {
                        filter: opacity(75%);
                    }
                }
            }
        }
    }

    @media (max-width: 1024px){
        & > #publications{
            & > h1{
                font-size: 42px;
            }
            & > div{
                & > div{
                    & > h1{
                        margin-left: 50px;
                    }
                    & > #title_weather{
                        margin-left: 0;
                    }
                }
            }
        }
    }

    @media (max-width: 768px){
        & > #publications{
            & > div{
                & > div{
                    & > h1{
                        font-size: 34px;
                    }

                    & > #title_weather{
                        font-size: 34px;
                    }
                    & > div{
                        width: 70%;
                    }
                }
            }
        }
    }

    @media (max-width: 425px){
        & > #publications{
            & > div{
                flex-direction: column;

                & > div{
                    width: 100%;

                    & > h1{
                        margin-right: 25px;
                    }

                    & > #title_weather{
                        margin-left: 35px;
                    }
                }
            }

            & > #weather{
                height: max-content;
                & > div{
                    & > #weather_img{
                        margin-top: 300px;
                    }
                }
            }
        }
    }

    @media (max-width: 375px){
        & > #publications{
            & > div{
                flex-direction: column;

                & > div{
                    width: 100%;

                    & > div{
                        width: 100%;
                    }

                    & > h1{
                        margin-right: 25px;
                    }

                    & > #title_weather{
                        text-align: center;
                    }
                }
            }
        }
    }

    @media (max-width: 320px){
        & > #publications{
            & > h1{
                font-size: 36px;
            }
            & > div{
                flex-direction: column;

                & > div{
                    width: 100%;

                    & > div{
                        width: 100%;
                    }

                    & > h1{
                        margin-right: 25px;
                    }

                    & > #title_weather{
                        text-align: center;
                    }
                }
            }
        }
    }
`

export const StyledLink = {
    width: "100%",
    height: "100%",
    display: "flex",
    justifyContent: "center",
    alignItems: " center",
}

export const Icons = {
    marginTop: "30px",
    height: "50px",
    width: "50px",
    color: `${colors.contrast}`
}

export const StyledImg = {
    width: "100%",
    height: "100%",
}